import React, { useEffect, useState } from "react";
import { isIframe, isFullScreen, detectMobile } from "../../../utils";

const Fullscreen = () => {
    const [isShow, setIsShow] = useState(true);

    const enableFullScreen = (event) => {
        event.preventDefault();
        const elem = document.documentElement;
        if (elem.requestFullscreen) {
            elem.requestFullscreen();
        } else if (elem.mozRequestFullScreen) {
            elem.mozRequestFullScreen();
        } else if (elem.webkitRequestFullscreen) {
            elem.webkitRequestFullscreen();
        } else if (elem.msRequestFullscreen) {
            elem.msRequestFullscreen();
        }
        setIsShow(false);
    };

    useEffect(() => {
        const screenTimeOut = setTimeout(() => setIsShow(false), 8000);
        return () => {
            clearTimeout(screenTimeOut);
        };
    }, []);
    return (
        (isShow && !detectMobile() && !window.location.href.includes('studio=1') && !isIframe && isFullScreen()) ? (
            <div className="tb_f_s_btn" onClick={enableFullScreen}>
                <div className="tb__icon tb-expand"><div></div></div>
                Enable Full Screen
            </div>
        ) : null
    );
};

export default Fullscreen;